.card-container {
  height: 18.2rem;
  overflow: hidden;
  box-shadow: 0px 0px 15px -5px;
  margin: 5%;
  cursor: pointer;
  display: flex;
  align-items: flex-start;
  justify-content: space-around;
  border-radius: 5px;
}

.card-container:hover {
  transform: scale(1.1);
  box-shadow: 0px 0px 15px 0px;
}

.menu-card-pic {
  overflow: hidden;
  width: 100%;
  height: 25vh;
}

.card-content {
  margin: 1rem;
  margin-top: 0.3rem;
}

h3, p {
  margin: 0;
  padding: 0;
  font-size: 1.5rem;
}


