.card {
  width: 30vh;
  height: 20rem;
  overflow: hidden;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: space-around;
  -webkit-box-shadow: none;
}

.front,
.back {
  width: 15rem;
  height: 19rem;
  overflow: hidden;
  backface-visibility: hidden;
  position: absolute;
  transition: transform .6s linear;
  border: 1px solid black;
  border-radius: 5px;
  box-shadow: 0px 0px 15px -5px;
}

.front {
  transform: perspective(600px) rotateY(0deg);
}

.back {
  transform: perspective(600px) rotateY(180deg);
}

.flipped .front {
  transform: perspective(600px) rotateY(-180deg);
}

.flipped .back {
  transform: perspective(600px) rotateY(0deg);
}

.card-pic {
  overflow: hidden;
  width: 100%;
  height: 18vh;
}

.card-container-back {
  width: 100%;
  text-align: center;
}

.card-title {
  display: flex !important;
  justify-content: space-around;
}

button {
  border: none !important;
  cursor: pointer;
}