.header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: aqua;
  width: 100%;
}

.wrapper .header-wrapper {
  display: inline-block;
  flex-wrap: nowrap;
}

nav {
  height: 3rem;
  position: fixed;
  z-index: 50;
  display: flex;
  align-items: center;
  left: 0;
  width: 0;
}

.burger-btn {
  width: 4rem;
  height: 2rem;
  margin-left: 1rem;
  position: absolute;
  cursor: pointer;
}

.burger-btn::after {
  content: '';
  position: absolute;
  top: 0;
  width: 2rem;
  background-color: orange;
  height: 0.2rem;
}

.burger-btn span {
  position: absolute;
  top: 0.9rem;
  width: 2rem;
  background-color: orange;
  height: 0.2rem;
}

.burger-btn::before {
  content: '';
  position: absolute;
  bottom: 0;
  width: 2rem;
  background-color: orange;
  height: 0.2rem;
}

.switch {
  position: relative;
  bottom: 1rem;
}

.btn {
  margin-left: 1rem;
  margin-top: 1rem;
}
.btn:hover {
  cursor: pointer !important;
  background-color: orange !important;
}