.menu {
  position: fixed;
  width: 100vh;
  height: 100vh;
  left: 0;
  transform: translateX(-100%);
  transition: all 0.7s;
  z-index: 49;
}

.menu.active {
  transform: translateX(0);
}

.menu-content {
  width: 30%;
  height: 185%;
  background-color: gray;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all 0.4s;
}

.menu-header {
  padding: 1rem;
  font-size: 2rem;
  color: white;
  border-bottom: 1px solid white;
  width: 100%;
}

.menu-link__item {
  color: white;
  text-decoration: none;
  font-size: 1.3rem;
  margin: 18%;
  white-space: nowrap;
}

.menu-link__item:hover {
  color: aqua;
}

.menu-list a {
  display: flex;
  align-items: center;
}

.menu-list {
  display: flex;
  flex-direction: column;
  text-align: center;
}